




















import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:'',
			activeindex:0,
			classlist:[],
			storelist:[],
			id:''
		}
	},
	onLoad(opt) {
		this.static=ut.static;
		this.id=opt._id;
		this.req_class(opt._id)
	},
	methods: {
		go_build_serch(){
			wx.navigateTo({
				url: '../build/serch'
			})
		},
		go_build_tenant(storeid,item){
			wx.setStorageSync('tenant',item)
			wx.navigateTo({
				url: `../build/tenant?storeid=${storeid}&classid=${this.classlist[this.activeindex].id}`
			})
		},
		cg_activeindex(index,classid){
			this.activeindex=index;
			this.req_storelist(classid)
		},
		req_class(id){
			ut.request({
				data: {
					parentid:id
				},
				url: "goods/class"
			}).then(data=>{
				this.classlist=data;
				return {
					classid:data[0].id
				}
			}).then(data=>{
				this.req_storelist(data.classid)
			})
		},
		req_storelist(classid){
			ut.request({
				data: {
					classid:classid,
					id:this.id
				},
				url: "goods/storelist"
			}).then(data=>{
				this.storelist=data;
			})
		},
	}
}
